.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.fontAgust {
  font-family: "Agustina Regular";
  font-weight: normal;
}
.fontLato {
  font-family: "Lato";
  font-weight: normal;
}
.fontSegoe {
  font-family: "Segoe UI Regular";
  font-weight: normal;
}
.calendar {
  width: 100%;
}
#root > div.css-19a1gle > div.css-d9gbhx > article > div > svg {
  width: 800px !important;
}
