.ArrowName {
  color: #868e96;
}

.linkActive {
  color: rgb(28, 126, 214);
  background-color: rgb(208, 235, 255);
}
.linkInActive {
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 18px;
  margin-right: 5px;
}
.linkInActive:hover {
  background-color: rgba(208, 235, 255, 0.548);
}
.menuDark {
  z-index: 1;
  flex-direction: column;
  top: 50px;
  width: 100%;
  position: fixed;
  outline: 0px;
  -webkit-tap-highlight-color: transparent;
  display: block;
  text-decoration: none;
  color: rgb(193, 194, 197);
  background-color: rgb(26, 27, 30);
  box-sizing: border-box;
  border-radius: 0px 0px 4px 4px;
  box-shadow: none;
  border-width: 0px 1px 1px;
  border-style: solid;
  border-color: rgb(55, 58, 64);
  border-image: initial;
  position: absolute;
  left: 0px;
  right: 0px;
  z-index: 0;
  overflow: hidden;
}
.menuLight {
  z-index: 1;
  flex-direction: column;
  top: 50px;
  width: 100%;
  position: fixed;
  border-color: rgb(222, 226, 230);
  background-color: rgb(255, 255, 255);
  color: black;
}
.siteTitle {
  cursor: pointer;
}

.lighNavbarDiv {
  position: sticky;
  top: 0px;
  background-color: #f1f3f5;
}
.darkNavbarDiv {
  position: sticky;
  top: 0px;
  background-color: #1a1b1e;
}
